<template>
  <div>
    <div class="app-main" v-if="!dialogFormVisible">
      <el-row type="flex" justify="center">
        <el-col :span="23">
          <el-card class="box-card" shadow="never">
            <div slot="header" class="clearfix">
              <span style="font-size: 20px;">设备绑定</span>
              <el-button-group style="float: right;">
                <!-- <el-button type="primary" icon="el-icon-plus">Add</el-button> -->
                <el-button type="primary" @click="refreshOnlineDev">
                  刷新
                  <i class="el-icon-refresh el-icon--right"></i>
                </el-button>
              </el-button-group>
            </div>
            <el-table :data="onlinedev" style="width: 100%;" size="mini">
              <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-form label-position="left" inline class="demo-table-expand">
                    <el-form-item v-for="(v,n) in scope.row.dev" :key="n" :label="n">
                      <span>{{ v }}</span>
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
              <el-table-column prop="SN" label="SN"></el-table-column>
              <el-table-column label="管理IP">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.host.IP" size="mini"/>
                </template>
              </el-table-column>
              <el-table-column prop="host.Port" label="管理Port"></el-table-column>
              <el-table-column prop="host.Name" label="名称"></el-table-column>
              <el-table-column prop="host.Alias" label="别名"></el-table-column>
              <el-table-column prop="host.Type" label="类型"></el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    type="success"
                    size="mini"
                    @click="handleEditOK(scope.$index, scope.row)"
                  >完成</el-button>
                </template>
              </el-table-column>
            </el-table>

            <el-alert style="margin:30px 0" type="info" :closable="false">
              <div solt="description">
                <p>修改管理IP后，需要重启整个设备才会生效</p>
              </div>
            </el-alert>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <el-dialog
      width="520px"
      title="登录"
      :visible.sync="dialogFormVisible"
      :show-close="false"
      center
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-form
        ref="loginForm"
        :model="loginForm"
        :rules="loginRules"
        class="login-form"
        auto-complete="on"
      >
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            name="username"
            type="text"
            auto-complete="on"
            placeholder="username"
          />
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            name="password"
            auto-complete="on"
            placeholder="password"
            @keyup.enter.native="handleLogin"
          />
        </el-form-item>
        <el-form-item>
          <el-button
            :loading="loading"
            type="primary"
            style="width:100%;"
            @click.native.prevent="handleLogin"
          >Sign in</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import md5 from "js-md5";

export default {
  data() {
    const validatePass = (rule, value, callback) => {
      if (value.length < 5) {
        callback(new Error("密码不能小于5位"));
      } else {
        callback();
      }
    };
    return {
      dialogFormVisible: true,
      formLabelWidth: "120px",
      loginForm: {
        username: "",
        password: ""
      },
      loginRules: {
        password: [{ required: true, trigger: "blur", validator: validatePass }]
      },
      loading: false,
      onlinedev: []
    };
  },
  methods: {
    handleLogin() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true;
          const username = this.loginForm.username.trim();
          const password = this.loginForm.password.toUpperCase();
          axios
            .post("/login", { userName: username, pwd: md5(password) })
            .then(response => {
              const res = response.data;
              if (res.code != 2000) {
                this.$message({
                  message: res.message,
                  type: "error",
                  duration: 5 * 1000
                });
              } else {
                this.dialogFormVisible = !this.dialogFormVisible;
                this.refreshOnlineDev();
              }
            })
            .catch(error => {
              this.$message({
                message: error.message,
                type: "error",
                duration: 5 * 1000
              });
            });
          this.loading = false;
        } else {
          return false;
        }
      });
    },
    handleEditOK(index, row) {
      const reg = /^((25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))\.){3}(25[0-5]|2[0-4]\d|((1\d{2})|([1-9]?\d)))$/;
      if (!reg.test(row.host.IP)) {
        this.$message({
          message: "请检查你输入的IP地址是否为正确的格式！",
          type: "error",
          duration: 5 * 1000
        });
        return;
      }

      axios
        .post("/changehost", row)
        .then(response => {
          const res = response.data;
          if (res.code != 2000) {
            this.$message({
              message: res.message,
              type: "error",
              duration: 5 * 1000
            });
          } else {
            this.$message({
              message: res.message,
              type: "success",
              duration: 5 * 1000
            });
            this.refreshOnlineDev();
          }
        })
        .catch(error => {
          this.$message({
            message: error.message,
            type: "error",
            duration: 5 * 1000
          });
        });
        this.refreshOnlineDev()
    },
    refreshOnlineDev() {
      axios
        .get("/onlinedev")
        .then(response => {
          const res = response.data;
          if (res.code != 2000) {
            this.$message({
              message: res.message,
              type: "error",
              duration: 5 * 1000
            });
          } else {
            this.onlinedev = res.data.eqlist;
          }
        })
        .catch(error => {
          this.$message({
            message: error.message,
            type: "error",
            duration: 5 * 1000
          });
        });
    }
  }
};
</script>

